<template>
    <div class='item' v-bind:class='{done: td.completed}'>
        <div class='flex'>
            <div class='content'>
                <button class="checkbox"
                    v-bind:class='{checked: td.completed}'
                    v-if='checkbox'
                    @click='td.completed = !td.completed'></button>
                <div>
                    <span
                        v-if='checkbox' 
                        class="title" 
                        contenteditable="true"
                        @keydown.enter="updateTask($event, td)"
                        @blur="updateTask($event, td)"
                        >{{td.content}}</span>
                    <span v-else>{{td.content}}</span>
                </div>
            </div>
            <button 
                class='remove-btn'
                v-if='checkbox'
                @click="$emit('remove-todo',td.id)">&times;</button>
        </div>
    </div>
</template>

<script>
export default {
    props: ['td', 'checkbox'],

    data(){
        return {
            beforeChange: ''
        }
    },

    created(){
        this.beforeChange = this.td.content || ''
    },

    methods: {

        // Update Todo task value
        updateTask(e, td){
            e.preventDefault();
            if(e.target.innerText.length === 0){
                e.target.innerText = this.beforeChange
            }
            td.content = e.target.innerText;

            e.target.blur();
        }
    }
}
</script>

<style scoped>
    .item{
        padding: 18px;
        margin: 6px auto;
        background: rgba(255, 255, 255, .5);
        border-radius: 10px;
        width: 90%;
        overflow: hidden;
    }    
    .done{
        background: rgba(0,0,0,.5);
        color: #fff;
    } 
    .flex{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .content{
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    .checkbox{
        background: none;
        border: 2px solid rgba(0,0,0,.5);
        border-radius: 3px;
        width: 20px;
        height: 20px;
        margin-right: 10px;
        cursor: pointer;
        outline: none;
    }
    .checked{
        background-image: url(data:image/svg+xml;utf8;base64,);
        width: 20px;
        height: 20px;
        background-size: 100%;
        background-repeat: no-repeat;
    }
    .title:focus{
        width: 230px;
        border: none;
        border-bottom: 2px solid #000;
        background: none;
        padding: 10px;
        outline: none;
        font-size: 16px;
    }
    .remove-btn{
        border: none;
        background: none;
        outline: none;
        cursor: pointer;
        font-size: 30px;
        color: rgba(0,0,0,.5);
    }
</style>
